<template>
    <div :class="{'border border-red-500 rounded-sm p-2 mb-2': hasErrors}">
        <input-errors :errors="errors"></input-errors>
        <div class="flex my-3">
            <div class="w-36">
                <slot name="label" v-if="label">
                    <label :class="{'text-red-800': hasErrors}">{{label}}:</label>
                </slot>
            </div>
            <slot>
                <input 
                    :type="type" 
                    :value="modelValue" 
                    @input="$emit('update:modelValue', $event.target.value)"
                    :placeholder="placeholder"
                >
            </slot>
        </div>
    </div>
</template>
<script>
import InputErrors from './InputErrors'

export default {
    components: {
        InputErrors
    },
    props: {
        errors: {
            type: Array,
            required: false,
            default: () => []
        },
        label: {
            type: String,
            required: false
        },
        type: {
            type: String,
            required: false,
            default: 'text'
        },
        modelValue: {
            required: false,
            default: null
        },
        placeholder: {
            required: false,
            value: null
        }
    },
    emits: [
        'update:modelValue'
    ],
    computed: {
        hasErrors () {
            return this.errors.length > 0;
        }
    }
}
</script>